import React, { Component } from 'react';
import { ListGrid, ListItem, SkillSet, TechnicalSkillContainer, TechnicalSkillWrapper } from './TechnicalSkills.style';
import { languages } from './Languages';
import { tools } from './Tools';

class TechnicalSkills extends Component {
  renderLanguages() {
    const languageItems = languages.map((language) =>
      <ListItem key={language.key}>{language.lang} - {language.experience}</ListItem>
    )
    return (
      <ListGrid>
        {languageItems}
      </ListGrid>
    );
  }

  renderTools() {
    const toolItems = tools.map((tool) =>
      <ListItem key={tool.key}>{tool.name}</ListItem>
    )
    return (
      <ListGrid>
        {toolItems}
      </ListGrid>
    );
  }

  render() {
    return (
      <TechnicalSkillWrapper>
        <h2>Technical Skills</h2>
        <TechnicalSkillContainer
          row
          justify={'center'}
        >
          <SkillSet>
            <span className="fa-stack fa-lg">
              <i className="fa fa-circle fa-stack-2x"></i>
              <i className="fa fa-file-code-o fa-stack-1x fa-inverse"></i>
            </span>
            <h4>Languages</h4>
            {this.renderLanguages()}
          </SkillSet>
          <SkillSet>
            <span className="fa-stack fa-lg">
              <i className="fa fa-circle fa-stack-2x"></i>
              <i className="fa fa-wrench fa-stack-1x fa-inverse"></i>
            </span>
            <h4>Tools</h4>
            {this.renderTools()}
          </SkillSet>
        </TechnicalSkillContainer>
      </TechnicalSkillWrapper>
    );
  }
}

export default TechnicalSkills;


